import React, {Component} from 'react'
import { connect } from 'react-redux'

import './styles.css'

class ButtonMenu extends Component {

    changeButtonClass(e){

        this.props.changeClass(e.target.className);



    }


    render(){

        return (
            <button className={this.props.className} onClick={this.changeButtonClass.bind(this)}>
                <span>

                </span>
            </button>
        )
    }

}

export default connect(
    state=>({

        className: state.navigation.classes.classButton

    }),

    dispatch=>({

        changeClass: (button)=>{

            dispatch({type:"CHANGE_MENU_BUTTON_CLASS", payload:button})

        },

        changeHeaderClass: (buttonClassName)=>{

            dispatch({type:"CHANGE_MOBILE_HEADER_CLASS", payload:buttonClassName})

        }
    })

)(ButtonMenu)


